import axios from "axios";

export default {
 
  getLights: function() {
    return axios.get("/api/lights");
  },
  
  loadLights: function(id) {
    return axios.post("/api/lights/" + id);
  },
  
  addLights: function(id) {
    return axios.add("/api/lights/" + id);
  },
 
  saveLights: function(addressData) {
    return axios.post("/api/lights", addressData);
  }
};